const clean = (text: string, type: string) => {
    // Remove ```yaml and ```
    text = text.replace(new RegExp(`^\`\`\`${type}`), "");

    // Strip all ` and ' from start and end
    text = text.replace(/^`+/, "");
    text = text.replace(/`+$/, "");
    return text;
}

function tryParseJSON<T>(text: string): { success: true, value: T } | { success: false, error: unknown } {
    try {
        return { success: true, value: JSON.parse(text) };
    } catch (e) {
        return { success: false, error: e };
    }
}

