type CompletionResult =
    { completable: true, completedJson: string } |
    { completable: false };

function isJSONObjectCompletable<T>(json: string): CompletionResult {
    // Stack to keep track of opening brackets/braces/quotes
    const stack: Array<'{' | '[' | '"'> = [];
    let inString = false;
    let escape = false;

    for (const char of json) {
        if (escape) {
            // Skip the next character if it's escaped
            escape = false;
        } else if (char === '\\') {
            escape = true;
        } else if (inString) {
            if (char === '"') {
                // End of string
                inString = false;
                stack.pop(); // Pop the opening quote from the stack
            }
        } else {
            if (char === '"') {
                // Start of string
                inString = true;
                stack.push('"'); // Push the opening quote onto the stack
            } else if (char === '{' || char === '[') {
                // Opening brace or bracket
                stack.push(char);
            } else if (char === '}' || char === ']') {
                // Closing brace or bracket
                const last = stack.pop();
                if ((char === '}' && last !== '{') || (char === ']' && last !== '[')) {
                    // Mismatched closing brace/bracket
                    return { completable: false };
                }
            }
        }
    }

    // Attempt to complete the JSON by closing all open brackets/braces and possibly a string
    let completedJson = json;
    let addedStringCompletion = false;
    while (stack.length > 0) {
        const last = stack.pop();
        if (last === '{') {
            completedJson += '}';
        } else if (last === '[') {
            completedJson += ']';
        } else if (last === '"' && !addedStringCompletion) {
            // Allow for one string completion
            completedJson += '"';
            addedStringCompletion = true;
        } else {
            // This should not happen if the JSON was well-formed to begin with
            return { completable: false };
        }
    }

    // Try to parse the completed JSON to ensure it's valid
    try {
        JSON.parse(completedJson);
        return { completable: true, completedJson };
    } catch (e) {
        return { completable: false };
    }
}

export async function* streamJSONObject<T>(
    generator: AsyncGenerator<{
        token: string;
        whole: string;
        done: boolean;
    }, void, unknown>
): AsyncGenerator<{ done: boolean, value: T }, void, unknown> {
    let pos: number = 1;
    let text = "";

    for await (const data of generator) {
        console.log("Parsing:", data);
        for (let i = pos; i <= data.whole.length; i++) {
            // Move forward
            pos = i;

            // Get the cleaned text
            text = clean(data.whole.slice(0, i), "json");

            // // Attempt to parse the JSON with } closing it.
            // const parsedCommand = tryParseJSON<T>(text + "}");

            // // Attempt to parse the JSON with "} closing it
            // const parsedCommandString = tryParseJSON<T>(text + "\"}");

            // // Check if we have a valid object to yield
            // if (parsedCommand.success) {
            //     // We have a valid object to yield
            //     yield { done: data.done, value: parsedCommand.value };
            //     continue; // Once a valid JSON is found, break the loop
            // }

            // if (parsedCommandString.success) {
            //     // We have a valid object to yield
            //     yield { done: data.done, value: parsedCommandString.value };
            //     continue; // Once a valid JSON is found, break the loop
            // }


            const completionResult = isJSONObjectCompletable<T>(text);

            if (completionResult.completable) {
                // We have a completable JSON, attempt to parse it
                const parsedCommand = tryParseJSON<T>(completionResult.completedJson);

                if (parsedCommand.success) {
                    // We have a valid object to yield
                    yield { done: data.done, value: parsedCommand.value };
                    continue; // Once a valid JSON is found, break the loop
                }
            }
        }
    }

    console.log("Done generating!");

    // Yield the final object if it's valid
    const finalCommand = tryParseJSON<T>(text);
    if (!finalCommand.success) {
        throw new Error("Invalid final text: " + text);
    }

    console.log("Yielding final element:", finalCommand.value);
    yield { done: true, value: finalCommand.value };
}
