import { appendFileSync } from "fs";

export type Tree<T extends Node> = {
    root: string;
    tree: {
        [id: string]: { obj: T, parent: string, children: string[] }
    }
}
export type Node = { id: string };

// Define some helper functions for dealing with the tree
export function getRoot<T extends Node>(tree: Tree<T>): T {
    return tree.tree[tree.root].obj;
}

export function getNode<T extends Node>(tree: Tree<T>, id: string, throwOnFail: boolean = true): T | undefined {
    const node = tree.tree[id];
    if (!node) {
        return undefined;
    }
    return node.obj;
}

export function setNode<T extends Node>(tree: Tree<T>, id: string, obj: T): void {
    if (!tree.tree[id]) {
        throw new Error(`Node ${id} does not exist in the tree`);
    }
    tree.tree[id].obj = obj;
}

export function getAncestry<T extends Node>(tree: Tree<T>, id: string): T[] {
    let node = tree.tree[id];
    let ancestry: T[] = [];
    while (node) {
        ancestry.push(node.obj);
        node = tree.tree[node.parent];
    }
    ancestry.reverse();
    return ancestry;
}

export function getChildren<T extends Node>(tree: Tree<T>, id: string): T[] {
    return tree.tree[id].children.map(id => tree.tree[id].obj);
}

export function getParent<T extends Node>(tree: Tree<T>, id: string): T {
    return tree.tree[tree.tree[id].parent].obj;
}

export function getDescendants<T extends Node>(tree: Tree<T>, id: string): T[] {
    let descendants: T[] = [];
    for (let child of tree.tree[id].children) {
        descendants.push(tree.tree[child].obj);
        descendants.push(...getDescendants(tree, child));
    }
    return descendants;
}

export function addChild<T extends Node>(tree: Tree<T>, parent: string, child: T): void {
    tree.tree[child.id] = { obj: child, parent, children: [] };
    tree.tree[parent].children.push(child.id);
}

export function removeNode<T extends Node>(tree: Tree<T>, node: string): void {
    // Remove the child from its parent's children
    let parent = tree.tree[node].parent;
    tree.tree[parent].children = tree.tree[parent].children.filter(id => id !== node);

    // Delete the child and its descendants from the tree
    let descendants = getDescendants(tree, node);
    descendants.push(tree.tree[node].obj);
    for (let descendant of descendants) {
        delete tree.tree[descendant.id];
    }
}