import React, { useState, useCallback, useMemo, useEffect, useContext } from 'react';
import {
    ASSISTANT_SYSTEM_MESSAGE,
    SUMMARIZER_SYSTEM_MESSAGE,
    ANALYSIS_SYSTEM_MESSAGE
} from '../prompts';
import { useSettings } from '../Settings';
import { produce } from 'immer';
import { Analysis, Conversation, Message } from "./types";
import { OpenAIConversationModel } from "./openai";
import db from './db'; // Import the Dexie database instance
import { Tree, Node, getRoot, getNode, setNode, getAncestry, getChildren, getDescendants, addChild, removeNode, getParent } from './tree';

export type State = {
    loaded: boolean;
    // conversations: { [key: string]: Conversation };
    conversations: { [key: string]: Conversation };
    currentMessageID: string;
    currentConversationID: string;
    respondToQueue: string[];
    postProcessQueue: string[];
    trash: string[];
};

export const loadConversations = async (): Promise<{ [key: string]: Conversation }> => {
    const conversations: Conversation[] = await db.conversations.toArray();
    const ret: { [key: string]: Conversation } = {};
    for (const conversation of conversations) {
        ret[conversation.id] = conversation;
    }
    return ret;
}

export const reconcileState = async (state: State): Promise<void> => {
    // Delete all nodes and roots that are not in the state
    await db.transaction('rw', db.conversations, async () => {
        // Add or update all conversations
        await db.conversations.bulkPut(Object.values(state.conversations));

        // Delete all conversations in the trash
        await db.conversations.bulkDelete(state.trash);
    });

    // Save the current conversation and message to local storage
    localStorage.setItem('currentConversation', state.currentConversationID);
    localStorage.setItem('currentMessage', state.currentMessageID);
}

type ConversationAction =
    // CRUD for roots
    | { type: "create-conversation" }
    | { type: "update-conversation", uuid: string, updateFn: (conversation: Conversation) => Conversation }
    | { type: "delete-conversation", uuid: string }

    // CRUD for nodes
    | { type: "create-message", parentID: string, message: Message }
    | { type: "update-message", uuid: string, updateFn: (message: Message) => Message }
    | { type: "delete-message", uuid: string }
    | { type: "fork-message", uuid: string }

    // Navigation
    | { type: "set-current-conversation", uuid: string }
    | { type: "set-current-message", uuid: string, recurse: boolean }

    // Response queue
    | { type: "responded-to", uuid: string }

    // Message post-processing
    | { type: "request-post-process", uuid: string }
    | { type: "started-post-process", uuid: string }

    // Other
    | { type: "load-state", state: State };

const initialState: State = {
    loaded: false,
    respondToQueue: [],
    postProcessQueue: [],
    conversations: {
        "test": {
            id: "test",
            name: "Test Conversation",
            summary: "Test conversation summary",
            tree: {
                root: "testMessage",
                tree: {
                    "testMessage": {
                        obj: {
                            type: "system",
                            id: "testMessage",
                            processing: {
                                summary: undefined,
                                analysis: undefined
                            },
                            text: ASSISTANT_SYSTEM_MESSAGE,
                            attachments: []
                        },
                        parent: "",
                        children: []
                    }
                }
            }
        }
    },
    currentMessageID: "testMessage",
    currentConversationID: "test",
    trash: []
};

const ConversationContext = React.createContext<{
    state: State;
    dispatch: React.Dispatch<ConversationAction>;
    actions: {
        // userResponse: (nodeUUID: string, response: string, images: GPTImage[], role: USER) => void;
        // Ask the model for a response to a message
        getResponse: (toID: string) => void;
        getChildren: (message: Message) => Message[];
        getParent: (message: Message) => Message | undefined;
        getAncestry: (message: Message) => Message[];
        // Duplicate a message
        forkMessage: (message: Message) => void;
        // Revise a message based on the analysis
        reviseMessage: (messageId: string) => void;
        // Get another response for a given message
        // rerunMessage: (message: Message) => void;
    };
    curConversation: Conversation | undefined;
    curMessage: Message | undefined;
}>({
    state: initialState,
    dispatch: () => { },
    actions: {
        getResponse: () => { },
        getChildren: () => [],
        getParent: () => undefined,
        getAncestry: () => [],
        forkMessage: () => { },
        reviseMessage: () => { }
        // rerunMessage: () => { }
    },
    curConversation: undefined,
    curMessage: undefined
});

const getConv = (draft: State) => draft.conversations[draft.currentConversationID];
const getCurMsg = (draft: State) => getNode(getConv(draft).tree, draft.currentMessageID);

const conversationReducer = produce((draft: State, action: ConversationAction) => {
    switch (action.type) {
        case "create-conversation":
            // Create a new root
            const conversationID = crypto.randomUUID();
            const rootMessageID = crypto.randomUUID();
            const rootMessage: Message = {
                type: "system",
                id: rootMessageID,
                // summary: "",
                processing: {
                    summary: undefined,
                    analysis: undefined
                },
                text: ASSISTANT_SYSTEM_MESSAGE,
                attachments: []
            }
            const newConversation: Conversation = {
                id: conversationID,
                name: "Dialog Root",
                summary: "Dialog tree summary",
                tree: {
                    root: rootMessageID,
                    tree: {
                        [rootMessageID]: {
                            obj: rootMessage,
                            parent: "",
                            children: []
                        }
                    }
                }
            };
            draft.conversations[conversationID] = newConversation;

            // Set the current message ID to the root message ID
            draft.currentConversationID = conversationID;
            draft.currentMessageID = rootMessageID;
            break;

        case "update-conversation":
            // draft.roots[action.uuid] = action.updateFn(draft.roots[action.uuid]);
            draft.conversations[action.uuid] = action.updateFn(draft.conversations[action.uuid]);
            break;

        case "delete-conversation":
            // Reset the current conversation and current message, if necessary
            if (draft.currentConversationID === action.uuid) {
                draft.currentConversationID = "";
                draft.currentMessageID = "";
            }

            // if (draft.currentMessageID === action.uuid) {
            //     draft.currentMessageID = "";
            // }

            // Delete the conversation
            delete draft.conversations[action.uuid];
            draft.trash.push(action.uuid);
            break;

        case "create-message":
            addChild(
                draft.conversations[draft.currentConversationID].tree,
                action.parentID,
                action.message
            )

            // If the message is a user message, auto generate a response to it
            if (action.message.type === "user") {
                draft.respondToQueue.push(action.message.id);
            }
            break;

        case "update-message":
            const msg = getNode(getConv(draft).tree, action.uuid);
            if (!msg) {
                return;
            }

            const newMessage = action.updateFn(msg);
            setNode(getConv(draft).tree, action.uuid, newMessage);
            break;

        case "delete-message":
            // If the current message is the deleted message, or one of its descendants,
            // set the current message to the parent of the deleted message
            const descendants = getDescendants(getConv(draft).tree, action.uuid);
            if (descendants.filter(descendant => descendant.id === draft.currentMessageID).length > 0) {
                const parent = getParent(getConv(draft).tree, action.uuid);
                if (parent) {
                    draft.currentMessageID = parent.id;
                } else {
                    // Default to the root of the conversation
                    draft.currentMessageID = getConv(draft).tree.root;
                }
            }

            // Delete the node
            removeNode(getConv(draft).tree, action.uuid);

            break;

        case "set-current-conversation":
            // Set the current conversation to the given conversation
            draft.currentConversationID = action.uuid;

            // Set the current message to the root message
            draft.currentMessageID = draft.conversations[action.uuid].tree.root;
            break;

        case "fork-message":
            // Get the message
            // const messageToDuplicate = draft.conversations[draft.currentConversationID].tree[action.uuid].message;
            const messageToDuplicate = getNode(getConv(draft).tree, action.uuid);
            if (!messageToDuplicate) {
                throw new Error(`Message with ID ${action.uuid} not found in state`);
            }
            if (messageToDuplicate.type === "system") {
                // Can't duplicate the system message
                throw new Error("Cannot duplicate system message");
            }

            // Create a new message
            const duplicate: Message = {
                ...messageToDuplicate,
                id: crypto.randomUUID(),
            };

            // Add the message to the tree
            addChild(
                draft.conversations[draft.currentConversationID].tree,
                getParent(getConv(draft).tree, action.uuid)?.id ?? "",
                duplicate
            );

            // Set the current message to the new message
            draft.currentMessageID = duplicate.id;
            break;

        case "set-current-message":
            // // Set the current message to the given message
            // draft.currentMessageID = action.uuid;

            // If the given message is a user message, and it has a response, set to that instead.
            // Otherwise, set to the given message.
            let newID = action.uuid;
            // const message = draft.conversations[draft.currentConversationID].tree[action.uuid].message;
            const message = getNode(getConv(draft).tree, action.uuid);
            if (!message) {
                throw new Error(`Message with ID ${action.uuid} not found in state`);
            }

            if (message.type === "user") {
                // Find if it has a response
                // const children = getChildMessages(draft.conversations[draft.currentConversationID], message);
                const children = getChildren(getConv(draft).tree, message.id);
                if (children.length === 1) {
                    // Set to the child instead
                    newID = children[0].id;
                }
            }

            // Set the current message
            draft.currentMessageID = newID;
            break;

        case "load-state":
            // Load the given state
            return action.state;

        case "responded-to":
            // Remove the message from the queue
            draft.respondToQueue = draft.respondToQueue.filter(uuid => uuid !== action.uuid);
            break;

        case "request-post-process":
            // Add the message to the post-processing queue
            draft.postProcessQueue.push(action.uuid);
            break;

        case "started-post-process":
            // Remove the message from the queue
            draft.postProcessQueue = draft.postProcessQueue.filter(uuid => uuid !== action.uuid);
            break;

        default:
            // Trick to ensure we've handled all actions
            // @ts-expect-error
            throw new Error(`Unknown action type: ${action.type}`);
    }
});

export const loadState = async (dispatch: React.Dispatch<ConversationAction>) => {
    // Load all roots and nodes
    const conversations = await loadConversations();

    // Load the current root and node from local storage
    const currentConversation = localStorage.getItem('currentConversation');
    const currentMessage = localStorage.getItem('currentMessage');

    console.log("Loaded state:", {
        conversations: conversations,
        currentConversation: currentConversation,
        currentMessage: currentMessage
    });
    dispatch({
        type: "load-state",
        state: {
            loaded: true,
            conversations: conversations,
            currentConversationID: currentConversation ? currentConversation : "",
            currentMessageID: currentMessage ? currentMessage : "",
            respondToQueue: [],
            postProcessQueue: [],
            trash: []
        }
    });
}

const ConversationProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const [state, dispatch] = React.useReducer(conversationReducer, initialState);
    const settings = useSettings();

    const curConversation = state.currentConversationID ? state.conversations[state.currentConversationID] : undefined;
    console.log("Getting node", state.currentMessageID, "from conversation", curConversation);
    const curMessage = (curConversation && state.currentMessageID) ? getNode(curConversation.tree, state.currentMessageID) : undefined;

    if (!curMessage) {
        // Set to the root of the conversation
        if (curConversation) {
            dispatch({
                type: "set-current-message",
                uuid: curConversation.tree.root,
                recurse: false
            });
        } else {
            // Set to the first conversation
            const firstConversation = Object.values(state.conversations)[0];
            dispatch({
                type: "set-current-conversation",
                uuid: firstConversation.id
            });
        }
    }
    // Load the database
    useEffect(() => {
        console.log("Loading state");
        loadState(dispatch);
    }, []);

    // Not very efficient, but it works for now
    useEffect(() => {
        reconcileState(state);
    }, [state]);

    const getResponse = useCallback(async (toID: string) => {
        if (!curConversation) {
            console.error("No current conversation!");
            return;
        }
        console.log("Responding to message:", toID);

        // Get the message chain
        // const message = state.conversations[state.currentConversationID].tree[toID]?.message;
        const message = getNode(curConversation.tree, toID);
        if (!message) {
            console.error(`Message with ID ${toID} not found in state:`, state);
            throw new Error(`Message with ID ${toID} not found in state`);
        }

        // Generate a summary for toID, if necessary
        if (message.type === "system") {
            throw new Error("Cannot (well... why would you?) generate response to system message!");
        }

        // Schedule a summary generation for the message
        dispatch({
            type: "request-post-process",
            uuid: message.id
        });

        const chain = getAncestry(curConversation.tree, message.id);

        const generator = OpenAIConversationModel.streamResponse(
            chain,
            settings.model
        );

        // Whenever we get a new response, update the state
        let last: Message | undefined = undefined;
        let i = 0;
        for await (const response of generator) {
            last = response;
            // If this is the first message (i.e. the tree doesn't contain the UUID),
            // add the UUID to the tree, setting the parent as appropriate
            if (i === 0) {
                // Create the new message
                dispatch({
                    type: "create-message",
                    parentID: message.id,
                    message: response
                });

                // Set the current message to the new message
                dispatch({
                    type: "set-current-message",
                    uuid: response.id,
                    recurse: false
                });
            }

            i++;

            dispatch({
                type: "update-message",
                uuid: response.id,
                updateFn: (message: Message) => {
                    // Set the message within the tree
                    return response;
                }
            });
        }

        if (!last) {
            throw new Error("No message generated!");
        }

        // Schedule a summary generation for the last message
        dispatch({
            type: "request-post-process",
            uuid: last.id
        });
    }, [state]);

    const generateSummary = useCallback(async (message: Message) => {
        if (!curConversation) {
            console.error("No current conversation!");
            return;
        }

        console.log("Summarizing message:", message);

        const ancestors = getAncestry(curConversation.tree, message.id);
        // ancestors.push(message);
        const generator = OpenAIConversationModel.streamSummary(ancestors, SUMMARIZER_SYSTEM_MESSAGE, settings.model);

        // Whenever we get a new response, update the state
        const fn = async () => {
            for await (const response of generator) {
                // If this is the first message (i.e. the tree doesn't contain the UUID),
                // add the UUID to the tree, setting the parent as appropriate
                dispatch({
                    type: "update-message",
                    uuid: message.id,
                    updateFn: (message: Message): Message => {
                        // Set the message within the tree
                        return {
                            ...message,
                            processing: {
                                ...message.processing,
                                summary: response
                            }
                        };
                    }
                });
            }
            console.log("Summary complete!");
        };

        fn();
    }, [state]);

    const generateAnalysis = useCallback(async (message: Message) => {
        if (!curConversation) {
            console.error("No current conversation!");
            return;
        }

        console.log("Analyzing message:", message);

        const ancestors = getAncestry(curConversation.tree, message.id);
        console.log("Ancestors:", ancestors);
        // ancestors.push(message);
        const analysisGenerator = OpenAIConversationModel.analyze(ancestors, ANALYSIS_SYSTEM_MESSAGE, settings.model)

        // // Set the analysis
        // console.log("Analysis complete:", analysis);
        // dispatch({
        //     type: "update-message",
        //     uuid: message.id,
        //     updateFn: (message: Message): Message => {
        //         // Set the message within the tree
        //         return {
        //             ...message,
        //             processing: {
        //                 ...message.processing,
        //                 analysis: analysis
        //             }
        //         };
        //     }
        // });

        for await (const analysis of analysisGenerator) {
            console.log("Analysis complete:", analysis);
            dispatch({
                type: "update-message",
                uuid: message.id,
                updateFn: (message: Message): Message => {
                    // Set the message within the tree
                    return {
                        ...message,
                        processing: {
                            ...message.processing,
                            analysis: analysis.value
                        }
                    };
                }
            });
        }
    }, [state]);


    // Respond to any messages as necessary
    useEffect(() => {
        if (state.respondToQueue.length > 0) {
            console.log("Responding to queue:", state.respondToQueue);
            const uuid = state.respondToQueue[0];
            dispatch({
                type: "responded-to",
                uuid: uuid
            });
            getResponse(uuid);
        }
    }, [state.respondToQueue]);

    // Post-process messages as necessary
    useEffect(() => {
        if (state.postProcessQueue.length === 0) {
            return;
        }

        const uuid = state.postProcessQueue[0];
        console.log("Post processing:", uuid);
        // Mark this uuid as being post-processed
        dispatch({
            type: "started-post-process",
            uuid: uuid
        });

        const message = getNode(getConv(state).tree, uuid);
        if (!message) {
            // throw new Error(`Message with ID ${uuid} not found in state`);
            console.error("Message with ID", uuid, "not found in state");
            return;
        }

        // For user messages, just summarize. For responses, summarize and analyze.
        if (!message.processing.summary) {
            generateSummary(message);
        }

        if (message.type === "response" && !message.processing.analysis) {
            generateAnalysis(message);
        }
    }, [state]);

    const getMessageChildren = useCallback((message: Message) => {
        if (!curConversation) {
            console.error("No current conversation!");
            return [];
        }

        // return getChildMessages(curConversation, message);
        return getChildren(curConversation.tree, message.id);
    }, [curConversation]);

    const getMessageParent = useCallback((message: Message) => {
        if (!curConversation) {
            console.error("No current conversation!");
            return undefined;
        }

        return getParent(curConversation.tree, message.id);
    }, [curConversation]);

    const getMessageAncestry = useCallback((message: Message) => {
        if (!curConversation) {
            console.error("No current conversation!");
            return [];
        }

        const ancestry = getAncestry(curConversation.tree, message.id);
        console.log("Found ancestry:", ancestry);
        return ancestry;
    }, [curConversation]);

    const forkMessage = useCallback((message: Message) => {
        console.log("Forking message:", message);
        if (message.type !== "user") {
            throw new Error("Cannot fork non-user message!");
        }
        dispatch({
            type: "fork-message",
            uuid: message.id,
        });
    }, [settings.model]);

    const reviseMessage = useCallback((messageId: string) => {
        // Create a new user message from the analysis content
        // Then get a response to the message

        // Get the message
        const message = getNode(getConv(state).tree, messageId);
        if (!message) {
            throw new Error(`Message with ID ${messageId} not found in state`);
        }

        // Get the analysis
        const analysis = message.processing.analysis;

        // Convert the analysis to text
        const analysisText = JSON.stringify(analysis, null, 4);

        const userMessageText = `
[Note - the user has requested a revised version of your previous message based on the following analysis.]

\`\`\`
${analysisText}
\`\`\`

Respond to this message with a revision of the previous message, speaking directly to the user.
`.trim();

        // Create a new message
        const newMessage: Message = {
            type: "user",
            id: crypto.randomUUID(),
            // summary: "",
            processing: {
                summary: undefined,
                analysis: undefined
            },
            text: userMessageText,
            attachments: []
        };

        // Add the message to the tree
        dispatch({
            type: "create-message",
            parentID: message.id,
            message: newMessage
        });

        // Set the current message to the new message
        dispatch({
            type: "set-current-message",
            uuid: newMessage.id,
            recurse: false
        });
    }, [state]);

    return (
        <ConversationContext.Provider value={{
            state: state,
            dispatch: dispatch,
            actions: {
                getChildren: getMessageChildren,
                getParent: getMessageParent,
                getAncestry: getMessageAncestry,
                getResponse,
                forkMessage,
                reviseMessage
                // rerunMessage
            },
            curConversation, curMessage
        }}>
            {children}
        </ConversationContext.Provider>
    );
};

export const useConversation = () => {
    const context = useContext(ConversationContext);
    if (context === undefined) {
        throw new Error('useConversation must be used within a ConversationProvider');
    }
    return context;
}

export default ConversationProvider;
