import { Card, Typography, Box, Button } from '@mui/material';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import WarningAmberIcon from '@mui/icons-material/WarningAmber';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import { Analysis, Message, Note, VALUE_PRIORITY_MAP } from '../conversation/types';
import { useConversation } from '../conversation/context';


const STATUS_MAP = {
    0: "#697A21", // green
    1: "#CC6F33", // darker yellow
    2: "#C52233", // red
    3: "#595F72", // gray
}

const capitalizeFirstLetter = (string: string | undefined) => {
    return string ? string.charAt(0).toUpperCase() + string.slice(1) : '';
};

export function getColor(analysis: Analysis | undefined): { correctColor: string, completeColor: string, hallucinationColor: string, totalColor: string } {
    if (!analysis) {
        return {
            correctColor: STATUS_MAP[3],
            completeColor: STATUS_MAP[3],
            hallucinationColor: STATUS_MAP[3],
            totalColor: STATUS_MAP[3],
        };
    }

    const correctValue = VALUE_PRIORITY_MAP[analysis.correct as keyof typeof VALUE_PRIORITY_MAP] as keyof typeof STATUS_MAP;
    const completeValue = VALUE_PRIORITY_MAP[analysis.complete as keyof typeof VALUE_PRIORITY_MAP] as keyof typeof STATUS_MAP;
    const hallucinatingValue = VALUE_PRIORITY_MAP[analysis.hallucinating as keyof typeof VALUE_PRIORITY_MAP] as keyof typeof STATUS_MAP;

    const value = Math.max(correctValue, completeValue, hallucinatingValue);

    const correctColor: string = STATUS_MAP[correctValue as keyof typeof STATUS_MAP];
    const completeColor: string = STATUS_MAP[completeValue as keyof typeof STATUS_MAP];
    const hallucinationColor: string = STATUS_MAP[hallucinatingValue as keyof typeof STATUS_MAP];
    const totalColor: string = STATUS_MAP[value as keyof typeof STATUS_MAP];
    return { correctColor, completeColor, hallucinationColor, totalColor };
}

const getSeverityIndex = (severity: string) => {
    switch (severity) {
        case 'good':
            return 0;
        case 'warning':
            return 1;
        case 'error':
            return 2;
        case 'unknown':
            return 3;
        default:
            return 3;
    }
}

const AnalysisCard = ({ title, value, icon, color }: { title: string, value: string | undefined, icon: JSX.Element, color: string }) => (
    <Card sx={{ p: 0.5, display: 'flex', alignItems: 'center', gap: 1, bgcolor: color + "22" }}>
        {icon}
        <Box>
            <Typography variant="subtitle2" color="textSecondary" sx={{ mb: -0.75}}>
                {title}
            </Typography>
            <Typography variant="h6" style={{ color }}>
                {capitalizeFirstLetter(value)}
            </Typography>
        </Box>
    </Card>
);

export const NoteInterior = ({ message, color }: { message: Message, color: string }) => {
    const { actions } = useConversation();
    const { processing } = message;
    const analysis = processing?.analysis;

    if (!analysis) {
        return null;
    }

    const { correctColor, completeColor, hallucinationColor } = getColor(analysis);

    // const sortedNotes = [...analysis.notes].sort((a, b) => {
    //     const aIndex = getSeverityIndex(a.severity);
    //     const bIndex = getSeverityIndex(b.severity);
    //     return aIndex - bIndex;
    // });
    // sortedNotes.reverse();

    const sortedNotes = analysis.notes;

    return (
        <Box
            sx={{ mt: 1, mb: 3, display: 'flex', flexDirection: 'column', alignItems: 'center' }}
        >
            <Box
                sx={{ display: 'flex', justifyContent: 'center', gap: 2, flexWrap: 'wrap', width: '90%', maxWidth: 800 }}
            >
                <AnalysisCard
                    title="Correctness"
                    value={analysis.correct}
                    color={correctColor}
                    icon={<CheckCircleOutlineIcon style={{ color: correctColor }} />}
                />
                <AnalysisCard
                    title="Completeness"
                    value={analysis.complete}
                    color={completeColor}
                    icon={<WarningAmberIcon style={{ color: completeColor }} />}
                />
                <AnalysisCard
                    title="Hallucination"
                    value={analysis.hallucinating}
                    color={hallucinationColor}
                    icon={<ErrorOutlineIcon style={{ color: hallucinationColor }} />}
                />
            </Box>
            {analysis.notes?.length > 0 && (
                <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1, alignItems: 'center', mt: 1 }}>
                    {sortedNotes.map((note: Note, i: number) => (
                        <NoteRenderer key={i} note={note} />
                    ))}
                </Box>
            )}
            <Button
                variant="contained"
                sx={{ mt: 2, bgcolor: "#e1e1e1", color: '#1e1e1e' }}
                onClick={() => {
                    actions.reviseMessage(message.id)
                }}
            >
                Revise
            </Button>
        </Box>
    );
};

const NoteRenderer = ({ note }: { note: Note }) => {
    // const bg = getSeverityColor(note.severity) + "22";
    const sevIndex = getSeverityIndex(note.severity);
    const sevColor = STATUS_MAP[sevIndex as keyof typeof STATUS_MAP];
    return (
        <Card sx={{ width: '100%', maxWidth: 800, p: 2, bgcolor: sevColor + "22" }}>
            <Typography variant="body1" style={{ color: sevColor }}>
                {note.text}
            </Typography>
        </Card>
    )
}