import { Tree } from "./tree";

export interface Conversation {
    id: string;
    name: string;
    summary: string;
    tree: Tree<Message>
}

// export type Message =
//     { type: "system", id: string, text: string } |
//     { type: "user", id: string, summary: string, text: string, attachments: Attachment[] } |
//     { type: "response", id: string, summary: string, text: string, attachments: Attachment[] };

export type Message = {
    type: "system" | "user" | "response";
    id: string;
    text: string;
    // summary: string;
    // analysis: Analysis;
    processing: {
        summary: string | undefined;
        analysis: Analysis | undefined;
    }
    attachments: Attachment[];
}

export type Attachment = {
    type: "image" | "file";
    name: string;
    base64: string;
};

export enum Correctness {
    GOOD = "good",
    MODERATE = "moderate",
    POOR = "poor",
    UNKNOWN = "unknown"
}

export enum Completeness {
    GOOD = "good",
    MODERATE = "moderate",
    POOR = "poor",
    UNKNOWN = "unknown"
}

export enum Hallucination {
    UNLIKELY = "unlikely",
    LIKELY = "likely",
    UNKNOWN = "unknown"
}

export const VALUE_PRIORITY_MAP = {
    good: 0,
    unlikely: 0,
    moderate: 1,
    likely: 2,
    poor: 2,
    unknown: 3
}

export type Analysis = {
    id: string;
    correct: Correctness;
    complete: Completeness;
    hallucinating: Hallucination;
    // notes: string[];
    notes: Note[];
}

export type Note = {
    name: string;
    severity: "good" | "warning" | "error" | "unknown";
    text: string;
}

export interface ConversationModel {
    id: string;
    name: string;
    models: string[];
    streamResponse: (messages: Message[], model: string) => AsyncGenerator<Message, void, unknown>;
    streamSummary: (messages: Message[], systemMessage: string, model: string) => AsyncGenerator<string, void, unknown>;
    analyze: (messages: Message[], systemMessage: string, model: string) => AsyncGenerator<{ done: boolean, value: Analysis }>;
}