import React, { useState, useCallback, useEffect } from 'react';
import { useConversation } from '../conversation/context';
import { useSettings } from '../Settings';
import { Button, Box, Chip, IconButton } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import { useEditor, EditorContent } from '@tiptap/react'
import StarterKit from '@tiptap/starter-kit'
import TurndownService from 'turndown';
import { useDropzone } from 'react-dropzone';
import './markdown.scss';
import { Attachment, Message } from '../conversation/types';



const FileList = ({ files, onDelete }: { files: Attachment[], onDelete: (file: Attachment) => void }) => {
    return (
        <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
            {files.map((file, index) => (
                <Chip
                    key={index}
                    label={
                        <div style={{ display: "flex", alignItems: "center" }}>
                            {file.type === "image" ? (
                                <img src={file.base64} style={{ height: "16px", width: "16px", padding: "2px", paddingRight: "8px", objectFit: "cover" }} />
                            ) : (
                                <span style={{ paddingRight: "8px" }}></span>
                            )}
                            <span>{file.name}</span>
                        </div>
                    }
                    onDelete={() => onDelete(file)}
                    size="small"
                    deleteIcon={
                        <IconButton size="small" onClick={() => onDelete(file)}>
                            <CloseIcon />
                        </IconButton>
                    }
                />
            ))}
        </Box >
    );
};

export default FileList;
export function ChatInput() {
    const settings = useSettings();
    const { state, dispatch, actions, curConversation, curMessage } = useConversation();
    const { getResponse } = actions;
    const [locked, setLocked] = React.useState(false);
    const [newMessage, setNewMessage] = useState<Message>({
        type: "user",
        id: "",
        text: "",
        // summary: "",
        processing: {
            summary: undefined,
            analysis: undefined
        },
        attachments: [],
    });

    const editor = useEditor({
        extensions: [
            StarterKit,
        ],
        content: newMessage.text,
    })

    useEffect(() => {
        if (!editor) {
            return;
        }

        // Reference: https://github.com/ueberdosis/tiptap/issues/2403#issuecomment-1017840603
        editor.off("update");
        editor.on("update", ({ editor: updatedEditor }) => {
            setNewMessage((prev) => {
                return {
                    ...prev,
                    text: updatedEditor.getHTML(),
                }
            });
        });
    }, [editor]);

    // When the message changes, update the editor
    useEffect(() => {
        if (!editor) {
            return;
        }

        if (editor.getHTML() !== newMessage.text) {
            editor.commands.setContent(newMessage.text);
        }
    }, [editor, newMessage]);

    const handleClick = useCallback(() => {
        if (!curMessage) {
            throw new Error("No current message");
        }

        // Convert from HTML to Markdown
        const markdown = new TurndownService().turndown(newMessage.text);

        // Create the message with a markdown version of the text
        const id = crypto.randomUUID();
        const submitMessage = {
            ...newMessage,
            id: id,
            text: markdown
        }
        dispatch({
            type: 'create-message',
            parentID: curMessage.id,
            message: submitMessage,
        });
        dispatch({
            type: 'set-current-message',
            uuid: id,
            recurse: true
        });
        // getResponse(id);
        // setHTML("");
        setNewMessage({
            type: "user",
            id: "",
            text: "",
            // summary: "",
            processing: {
                summary: undefined,
                analysis: undefined
            },
            attachments: [],
        })
        // setImages([]);
        const chatWindow = document.getElementById('chat-window');
        if (chatWindow) {
            setTimeout(() => {
                chatWindow.scrollTop = chatWindow.scrollHeight;
            }, 100);
        }
    }, [newMessage, dispatch, getResponse, newMessage]);

    const onDrop = useCallback(async (acceptedFiles: File[]) => {
        console.log("Accepted files:", acceptedFiles);
        console.log("Locking send button");
        setLocked(true);

        // Get the base64 encoding of each file
        const imageBase64promises: Promise<string>[] = acceptedFiles.map((file) => {
            return new Promise<string>((resolve, reject) => {
                const reader = new FileReader();
                reader.onload = () => {
                    resolve(reader.result as string);
                };
                reader.onerror = reject;
                reader.readAsDataURL(file);
            });
        });

        await Promise.all(imageBase64promises);

        // Create the image array
        const new_images: Attachment[] = [];
        for (let i = 0; i < acceptedFiles.length; i++) {
            const file = acceptedFiles[i];
            const base64 = await imageBase64promises[i];

            console.log("File " + file + " has base64:", base64);

            // Detect an image (with supported file formats)
            const isImage = file.type.startsWith("image/");
            if (isImage) {
                new_images.push({
                    type: "image",
                    name: file.name,
                    base64: base64,
                });
            } else {
                new_images.push({
                    type: "file",
                    name: file.name,
                    base64: base64,
                });

            }
        }

        console.log("New images:", new_images);

        // Add the images to the current message
        setNewMessage((prev) => {
            return {
                ...prev,
                attachments: [...prev.attachments, ...new_images],
            }
        });

        console.log("Unlocking send button");
        setLocked(false);
    }, []);

    const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop, noClick: true })

    if (!settings.openaiApiKey) {
        return (
            <div style={{
                paddingTop: "12px",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                textAlign: "center",
            }}>
                <h3>
                    Welcome! Please set your OpenAI API key in the settings panel (top-right corner).
                </h3>
            </div>
        );
    }

    console.log("Rendering message:", newMessage);
    return (
        <div {...getRootProps()} style={{ height: "100%", width: "100%" }}>
            <input {...getInputProps()} />
            {
                isDragActive &&
                <div style={{
                    height: "100%",
                    width: "100%",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                }}>
                    <span style={{ fontSize: "24px", fontWeight: "bold", marginBottom: "4px" }}>
                        Drop files here
                    </span>
                </div>
            }
            <div style={{
                height: "100%",
                display: "flex",
                flexDirection: "column",
                padding: "4px"
            }}
            >
                {newMessage.attachments.length > 0 && (
                    <div style={{ minHeight: "32px", width: "100%" }}>
                        <FileList
                            files={newMessage.attachments}
                            onDelete={(file) => {
                                setNewMessage((prev) => {
                                    return {
                                        ...prev,
                                        attachments: prev.attachments.filter((attachment) => attachment !== file),
                                    }
                                });
                            }}
                        />
                    </div>
                )}
                <EditorContent
                    editor={editor}
                    className="input"
                    style={{
                        height: newMessage.attachments.length > 0 ? "calc(100% - 132px)" : "calc(100% - 100px)",
                    }}
                    onKeyDown={(e) => {
                        if ((e.ctrlKey || e.metaKey) && e.key === 'Enter') {
                            handleClick();
                        }
                    }}
                />
                <div style={{ height: "8px" }} />
                {settings.warn ? (
                    <div style={{
                        height: "20px",
                        fontSize: '14px',
                        color: 'black',
                        // Center text in div
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        marginTop: "-6px",
                        paddingTop: "3px",
                        paddingBottom: "3px",
                    }}>
                        <i>AI systems give inaccurate information and are prone to bias. You use this app entirely at your own risk.</i>
                    </div>
                ) : null}
                <div style={{ minHeight: "30px" }}>
                    <Button onClick={handleClick}
                        style={{
                            padding: "0px",
                            marginTop: "0px",
                            width: "100%",
                            height: "100%",
                            color: "#f5f5f5"
                        }}
                    // disabled={!(state.currentRoot && state.currentNode) || locked}
                    >
                        Send
                    </Button>
                </div>
            </div>
        </div>
    );
}